
// export default ContestsList
import { Fragment, useState, useEffect } from 'react'
import UiLoader from '@components/ui-loader'
// ** Invoice List Sidebar

// ** Columns
import { columns } from './columns'
// import AddQuiz from '../add'
import Flatpickr from 'react-flatpickr'
// ** Store & Actions
import { useDispatch, useSelector } from 'react-redux'

// ** Third Party Components
import ReactPaginate from 'react-paginate'
import { ChevronDown } from 'react-feather'
import DataTable from 'react-data-table-component'
import '@styles/react/libs/flatpickr/flatpickr.scss'
import {
  Card, 
  //Input,
  Row,
  Col,
  Label,
  FormGroup,
  //Form,
  //CustomInput,
  Spinner,
  Button
} from 'reactstrap'

// ** Styles
import '@styles/react/libs/react-select/_react-select.scss'
import '@styles/react/libs/tables/react-dataTable-component.scss'
import {
  // getContestsListCSV,
  getcricketwinnerdata,
  getcricketwinnerdownload,
  winnerfrom,
  winnerto,
  contestfrom,
  contestto,
  quizfrom,
  quizto
  //getfootballcontestdownload 
} from '../../store/actions'
  
const ContestsList = () => {
  // ** Store Vars
  const CustomHeader = ({
    //handleFilter,
    //searchTerm,
    currentPage,
    setCurrentPage
  }) => {
    const store = useSelector((state) => state.rep)
    const [picker, setPicker] = useState(store.winner_from)
    const [dateto, setDateto] = useState(store.winner_to)
  const dispatch = useDispatch()
  const edittime = (date) => {
    const local = new Date(date)
    const datestring =  `${local.getFullYear()}-${(local.getMonth()) + 1}-${local.getDate()}`
    setPicker(datestring)
  }
 
  const edit = (date) => {
    const local = new Date(date)
    const datestring =  `${local.getFullYear()}-${(local.getMonth()) + 1}-${local.getDate()}`
    setDateto(datestring)
  }

  const onSubmit = (e) => {
    e.preventDefault()
    setCurrentPage(1)
    dispatch(getcricketwinnerdata(currentPage, picker, dateto))
    dispatch(winnerfrom(picker))
    dispatch(winnerto(dateto))
  }
    //curentpage
    const download = () => {
      dispatch(getcricketwinnerdownload())
    }
    return (
      <div className="invoice-list-table-header w-100 mr-1 ml-50 mt-2 mb-75">
        <Row>
        <Col  className="d-flex align-items-center p-0">
          <div className="d-flex align-items-center w-100">
            
            <Label className="mb-0">From:</Label>&nbsp;&nbsp;
            <Flatpickr
                  value={store.winner_from}
                  id='range-picker'
                  className='form-control'
                  //  value={picker}
                  //  onChange={date => setPicker(date)}
                  onChange={date => edittime(date)}
      
                 />
         &emsp;&nbsp;
            
            <Label className="mb-0">To:</Label>&nbsp;&nbsp;   
            <Flatpickr
                    value={store.winner_to}
                    id='range-picker'
                    className='form-control'
                    onChange={dat => edit(dat)}
                 /> 
             &emsp;&nbsp;
                  <FormGroup className="d-flex mb-0">
                <Button.Ripple
                  className="mr-1"
                  color="primary"
                  type="submit"
                  onClick={(e) => onSubmit(e)}
                >
                  Submit
                </Button.Ripple>
          </FormGroup>  
          </div>
        </Col>
          <Col
            xl="6"
            className="d-flex align-items-sm-center justify-content-lg-end justify-content-start flex-lg-nowrap flex-wrap flex-sm-row flex-column pr-lg-1 p-0 mt-lg-0 mt-1"
          >
            <div className="d-flex align-items-center mb-sm-0 mb-1 mr-1">
           
            </div>
                <Button
                color ='primary'
                onClick ={() => download()}
                // onClick = {getfootballcontestdownload()}
                >
                  Download CSV
                </Button>
          </Col>
        </Row>
      </div>
    )
  }

  const store = useSelector((state) => state.rep)
  const {cricket_winner} = store
  console.log(cricket_winner)
  // ** States
  const [searchTerm] = useState('')
  const [listData, setListData] = useState([])
  const [currentPage, setCurrentPage] = useState(1)
   const dispatch = useDispatch()
useEffect(() => {
    if (store.isloading) {
      <Spinner/>
    } else {
      setListData(store.cricket_winner)
    }
  }, [store.isloading])

  useEffect(() => {
    dispatch(getcricketwinnerdata(currentPage, store.winner_from, store.winner_to))
    dispatch(contestfrom(""))
    dispatch(contestto(""))
    dispatch(quizfrom(""))
    dispatch(quizto(""))
  }, [currentPage])

  const handlePagination = (page) => {
  
    setCurrentPage(page.selected + 1)
  }
 useEffect(() => {
    if (searchTerm) {
      const filterList = store.cricket_winner.filter((userItem) => {

        if (userItem.contest_name) {
          return userItem.contest_name.toLowerCase().includes(searchTerm.toLowerCase())
        }
      })

      setListData(filterList)
      setCurrentPage(1)
    } else {
      setListData(store.cricket_winner)
    }
  }, [searchTerm])

  if (store.isloading) {
    return (
      <div className="position-absolute d-flex justify-content-center align-items-center w-100 position-top-0 position-bottom-0 position-left-0 position-right-0 h-100">
        <UiLoader
          blocking
        />
      </div>
    )
  }

  // ** Custom Pagination
  const CustomPagination = () => {
    // const count = Number(Math.ceil(listData.length / rowsPerPage))
    //const count = store.cricket_contest_perpage.length
    // const count = store.cricket_contest_perpage.filter(page => page.label !== "&laquo; Previous" && page.label !== 'Next &raquo;').length
    const count = store.cricket_winner_perpage.last_page
      //console.log(count)
    return (
      <ReactPaginate
        previousLabel={''}
        nextLabel={''}
        pageCount={count || 1}
        activeClassName="active"
        forcePage={currentPage !== 0 ? currentPage - 1 : 0}
        onPageChange={(page) => handlePagination(page)}
        pageClassName={'page-item'}
        nextLinkClassName={'page-link'}
        nextClassName={'page-item next'}
        previousClassName={'page-item prev'}
        previousLinkClassName={'page-link'}
        pageLinkClassName={'page-link'}
        containerClassName={
          'pagination react-paginate justify-content-end my-2 pr-1'
        }
      />
    )
  }

  // ** Table data to render
  const dataToRender = () => {
    const filters = {
      q: searchTerm
    }

    const isFiltered = Object.keys(filters).some(function (k) {
      return filters[k].length > 0
    })
 
    if (listData.length === 0 && isFiltered) {
      return []
    } else {
      return listData
    }
  }

  return (
    
   <Fragment>
      <Card>  
        <DataTable
          noHeader
          pagination
          subHeader
          responsive
          paginationServer
          columns={columns}
          paginationPerPage
          paginationDefaultPage={currentPage}
          sortIcon={<ChevronDown />}
          className="react-dataTable"
          paginationComponent={CustomPagination}
          data={dataToRender()}
          subHeaderComponent={
            <CustomHeader
              //handlePerPage={handlePerPage}
              rowsPerPage
              searchTerm={searchTerm}
              currentPage={currentPage}
              setCurrentPage={setCurrentPage}
              // handleFilter={handleFilter}
            />
          }
        />
      </Card>
    </Fragment>
   
  )
}

export default ContestsList
